<script>
import img1 from "../assets/team-1.jpg";
import img2 from "../assets/team-2.jpg";
import img3 from "../assets/team-3.jpg";
import img4 from "../assets/team-4.jpg";

import { ref, onMounted } from "vue";
export default {
  setup() {
    const currentSlide = ref(1);
    const getSlideCount = ref(null);
    const autoPlayEnable = ref(true);
    const timeoutDuration = ref(5000);
    const carouselSlide = [
      "img-1",
      "img-2",
      "img-3",
      "img-4",
      "img-5",
      "img-6",
      "img-7",
      "img-8",
      "img-9",
    ];

    // next slide
    const nextSlide = () => {
      if (currentSlide.value === getSlideCount.value) {
        currentSlide.value = 1;
        return;
      }
      currentSlide.value += 1;
    };
    // prev slide
    const prevSlide = () => {
      if (currentSlide.value === 1) {
        currentSlide.value = 1;
        return;
      }
      currentSlide.value -= 1;
    };
    // span
    const goToSlide = (index) => {
      currentSlide.value = index + 1;
    };

    // autoplay
    const autoPlay = () => {
      setInterval(() => {
        nextSlide();
      }, timeoutDuration.value);
    };

    if (autoPlayEnable.value) {
      autoPlay();
    }
    onMounted(() => {
      getSlideCount.value = document.querySelectorAll(".baba").length;
      //  عددهم
      // console.log(getSlideCount.value);
    });

    return {
      carouselSlide,
      currentSlide,
      getSlideCount,
      nextSlide,
      prevSlide,
      goToSlide,
    };
  },
  data() {
    return {
      img1,
      courcsePic: [img1, img2, img3, img4],

      x: null,
      isDrag: false,
      current: 0,
      direction: 1,
      transitionName: "fade",
      show: false,
      slides: [
        { className: "blue" },
        { className: "red" },
        { className: "yellow" },
      ],
      imges: [
        {
          one: "../",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
        {
          one: "https://www.timeoutdubai.com/cloud/timeoutdubai/2021/09/11/hfpqyV7B-IMG-Dubai-UAE.jpg",
        },
      ],
    };
  },
  methods: {
    // mahmoud(dir) {
    //   this.direction = dir;
    // }
    // updateCoordinates(e) {
    //   const carosel = document.querySelector(".carosel");
    //   this.x = e.pageX;
    //   carosel.scrollLeft = e.pageX;
    // },
    slide(dir) {
      this.direction = dir;
      // dir === 1
      //   ? (this.transitionName = "slide-next")
      //   : (this.transitionName = "slide-prev");
      var len = this.slides.length;
      this.current = (this.current + (dir % len) + len) % len;
    },
  },
  mounted() {
    this.show = true;
    // addEventListener("mousemove", this.updateCoordinates);
  },
  components: {},
};
</script>

